import { Dep } from "./Dep"


export class Observer {
  constructor(data: any) {
    this.walk(data)
  }
  walk(data: any) {
    // 1. åˆ¤æ–­dataæ˜¯å¦æ˜¯å¯¹è±¡
    if (!data || typeof data !== 'object') {
      return
    }
    // 2.éåŽ†dataå¯¹è±¡çš„æ‰€æœ‰å±žæ€§
    Object.keys(data).forEach((key) => {
      this.defineReactive(data, key, data[key])
    })
  }
  defineReactive(obj: any, key: string, val: any) {
    let that = this
    // è´Ÿè´£æ”¶é›†ä¾èµ–, å¹¶å‘é€é€šçŸ¥
    let dep = new Dep()

    // å¦‚æžœæ˜¯valå¯¹è±¡,æŠŠvalå†…éƒ¨çš„å±žæ€§è½¬æ¢æˆå“åº”å¼å¯¹è±¡
    that.walk(val)
    Object.defineProperty(obj, key, {
      enumerable: true,
      configurable: true,
      get() {
        // æ”¶é›†ä¾èµ–
        Dep.target && dep.addSub(Dep.target)
        console.log(Dep.target)
        return val
      },
      set(newValue) {
        if (newValue === val) {
          return
        }
        val = newValue
        that.walk(newValue)
        // å‘é€é€šçŸ¥
        dep.notify()
      },
    })
  }
}
