import { Watcher } from './Watcher'

 export class Compiler {
  private el: any
  constructor(private vm: any) {
    this.el = vm.$el
    this.vm = vm
    this.compile(this.el)
  }
  // ç¼–è¯‘æ¨¡æ¿, å¤„ç†æ–‡æœ¬èŠ‚ç‚¹å’Œå…ƒç´ èŠ‚ç‚¹
  compile(el: any) {
    let childNodes = el.childNodes
    Array.from(childNodes).forEach((node: any) => {
      // å¤„ç†æ–‡æœ¬èŠ‚ç‚¹
      // console.log(node.nodeType, 'nodeType')
      if (this.isTextNode(node)) {
        this.compileText(node)
      } else if (this.isElementNode(node)) {
        // å¤„ç†å…ƒç´ èŠ‚ç‚¹
        this.compileElement(node)
      }

      // åˆ¤æ–­nodeèŠ‚ç‚¹,æ˜¯å¦æœ‰å­èŠ‚ç‚¹, å¦‚æžœæœ‰å­èŠ‚ç‚¹,è¦é€’å½’è°ƒç”¨compile
      if (node.childNodes && node.childNodes.length) {
        this.compile(node)
      }
    })
  }

  // ç¼–è¯‘å…ƒç´ èŠ‚ç‚¹, å‡ºæ¥æŒ‡ä»¤
  compileElement(node: any) {
    console.log(node.attributes)
    // éåŽ†æ‰€æœ‰çš„å±žæ€§èŠ‚ç‚¹
    Array.from(node.attributes).forEach((attr: any) => {
      // åˆ¤æ–­æ˜¯å¦æ˜¯æŒ‡ä»¤
      let attrName = attr.name
      if (this.isDirective(attrName)) {
        // v-text --> text
        attrName = attrName.substr(2)
        let key = attr.value
        this.update(node, key, attrName)
      }

      if (this.isEvent(attrName)) {
        // æ˜¯äº‹ä»¶onå¼€å¤´
        console.log(this.vm, 'attrName')
        let key = attr.value
        const dir = attrName.substring(3)
        this.eventHandler(node, this.vm, key, dir)
      }
    })
  }

  isEvent(attr: any) {
    console.log(attr)
    return attr.indexOf('on') === 0
  }

  update(node: any, key: any, attrName: any) {
    //@ts-ignore
    let updateFn = this[attrName + 'Updater']
    updateFn && updateFn.call(this, node, this.vm[key], key)
  }

  // å¤„ç† v-text æŒ‡ä»¤
  textUpdater(node: any, value: any, key: string) {
    node.textContent = value
    new Watcher(this.vm, key, (newValue) => {
      node.textContent = newValue
    })
  }
  // v-model
  modelUpdater(node: any, value: any, key: string) {
    node.value = value
    new Watcher(this.vm, key, (newValue) => {
      node.value = newValue
    })

    // åŒå‘ç»‘å®š
    node.addEventListener('input', () => {
      this.vm[key] = node.value
    })
  }

  // å¤„ç†v-html
  htmlUpdater(node: any, value: any, key: string) {
    node.innerHTML = value
    new Watcher(this.vm, key, (newValue) => {
      console.log(newValue, 'newValue')
      node.innerHTML = newValue
    })
  }

  // ç¼–è¯‘æ–‡æœ¬èŠ‚ç‚¹ï¼Œå‡ºæ¥å·®å€¼
  compileText(node: any) {
    // console.dir(node)
    let reg = /\{\{(.+?)\}\}/
    let value = node.textContent
    if (reg.test(value)) {
      let key = RegExp.$1.trim()
      node.textContent = value.replace(reg, this.vm[key])

      // åˆ›å»ºwatcherå¯¹è±¡, å½“æ•°æ®æ”¹å˜æ›´æ–°è§†å›¾
      new Watcher(this.vm, key, (newValue) => {
        node.textContent = newValue
      })
    }
  }

  // æ·»åŠ äº‹ä»¶
  eventHandler(node: any, vm: any, exp: any, dir: any) {
    const fn = vm.$options.methods && vm.$options.methods[exp]
    if (dir && fn) {
      node.addEventListener(dir, fn.bind(vm))
    }
  }

  // åˆ¤æ–­å…ƒç´ å±žæ€§æ˜¯å¦æ˜¯æŒ‡ä»¤
  isDirective(attrName: any) {
    return attrName.startsWith('v-')
  }

  // åˆ¤æ–­èŠ‚ç‚¹æ˜¯å¦æ˜¯æ–‡æœ¬èŠ‚ç‚¹
  isTextNode(node: any) {
    return node.nodeType === 3
  }

  // åˆ¤è¯»èŠ‚ç‚¹æ˜¯å¦æ˜¯å…ƒç´ èŠ‚ç‚¹
  isElementNode(node: any) {
    return node.nodeType === 1
  }
}
