export class Dep {
  constructor(private subs: any[] = []) {}
  // å­˜å‚¨æ‰€æœ‰çš„è§‚å¯Ÿè€…
  static target: any
  // æ·»åŠ è§‚å¯Ÿè€…
  addSub(sub: any) {
    if (sub && sub.update) {
      this.subs.push(sub)
    }
  }
  // å‘é€é€šçŸ¥
  notify() {
    this.subs.forEach((sub) => {
      sub.update()
    })
  }
}
